.container {
  padding-inline: 2rem;
}

.main {
  padding-top: 5rem;
}

.search {
  align-items: center;
  margin-bottom: 2.9rem;

  h1 {
    margin-right: 1.6rem;
    font-size: 3rem;
    font-weight: 700;
    line-height: 3rem;
    color: rgba(255, 255, 255, 1);
  }

  &__inputSearch {
    width: 90%;
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 2.16rem;
    color: rgba(149, 149, 149, 1);

    &::placeholder {
      font-size: 1.6rem;
      font-weight: 400;
      line-height: 2.16rem;
      color: rgba(149, 149, 149, 1);
    }
  }

  &__form {
    width: 46.9rem;
    margin-right: 1.6rem;
    padding: 1.2rem 2rem;
    border: .1rem solid rgba(149, 149, 149, 1);
    border-radius: 1.2rem;

    column-gap: .7rem;
  }

  &__btnLoop {
    width: 2rem;
    height: 2rem;
    background-image: url(../img/search.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &__checkList {
    column-gap: 1rem;
  }

  &__btnCheck {
    align-items: center;
    justify-content: center;
    width: 99%;
    height: 97%;
    padding: 1.2rem 2rem;
    border-radius: 1rem;
    background-color: #353542;
    transition: background-color .3s ease-in-out, box-shadow .3s ease-in-out;

    column-gap: 1rem;

    &_active {
      background-color: #212129;
      box-shadow: 0 .3rem 1.5rem 0 #84d52c33;
    }
  }

  & li {
    display: flex;
    align-items: center;
    justify-content: center;
    border-width: .1rem;
    border-style: solid;
    border-color: transparent;
    border-radius: 1rem;
    background-image: linear-gradient(174.36deg, rgba(132, 213, 44, .36) 11.39%, rgba(23, 23, 34, 0) 189.78%);
  }

  &__btnIcon {
    width: 2rem;
    height: 2rem;
    background-image: url(../img/flower.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &__btnCheck {
    font-size: 1.8rem;
    font-weight: 700;
    line-height: 2.43rem;
    color: white;
  }
}

.noUi-handle-lower .noUi-tooltip {
  display: none;
}

.noUi-handle {
  width: 1.6rem !important;
  height: 1.6rem !important;
  border: none;
  border-radius: 100%;
  background: linear-gradient(193.42deg, #85d62d 23.76%, #6bab2b 86.01%);
  box-shadow: none;
  cursor: pointer;

  &::before,
  &::after {
    display: none;
  }
}

.noUi-horizontal {
  height: .4rem !important;
}

.noUi-connect {
  background-color: #7acb22;
  box-shadow: 0 .3rem 1.5rem 0 #84d52c4f;
}

.noUi-target {
  border: none;
  background: #30303d;
  box-shadow: none;
}

.noUi-tooltip {
  display: flex;
  justify-content: center;
  padding: .2rem .5rem .2rem .5rem;
  border: none;
  border-radius: .3rem;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.16rem;
  color: white;
  background-color: rgba(48, 48, 61, 1);

  &::after {
    content: "";
    position: absolute;
    bottom: -.5rem;
    width: 1.1rem;
    height: .5rem;
    background-image: url(../img/polygon.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}

.noUi-horizontal .noUi-tooltip {
  bottom: 175%;
}

.slide {
  margin-bottom: 1.5rem;

  & > button {
    width: 4.8rem;
    height: 4.8rem;
    margin-left: auto;
    border-radius: 1rem;
    background-image: linear-gradient(174.36deg, rgba(132, 213, 44, .36) 11.39%, rgba(23, 23, 34, 0) 189.78%);

    margin-block: auto;;

    & > span {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 99%;
      height: 97%;
      border-radius: 1rem;
      background-color: rgba(53, 53, 66, 1);

      &::after {
        content: "";
        position: absolute;
        width: 2.4rem;
        height: 2.4rem;
        background-image: url(../img/filter_btn_right.png);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
      }
    }
  }

  &__price,
  &__proc,
  &__proc2 {
    width: 22.5rem;
    margin-bottom: 1.5rem;
  }

  &__keypress {
    max-width: 6.5rem;
    padding: .3rem .5rem;
    border: .1rem solid rgba(96, 96, 96, 1);
    border-radius: .3rem;
    font-size: 1.4rem;
    font-weight: 400;
    line-height: 1.89rem;
    color: white;
    text-align: center;
    background-color: rgba(33, 33, 41, 1);
  }

  & div:has(input[type="text"]) {
    justify-content: flex-end;
  }

  &__priceBlock {
    max-width: 22.5rem;

    & div:has(input[id="input-with-keypress-0-price"]) {
      justify-content: space-between;
      width: 100%;
    }

    &:not(:last-child) {
      margin-right: 2.7rem;
    }

    & h3 {
      max-width: 10rem;
      margin-bottom: 2rem;
      font-size: 1.6rem;
      font-weight: 600;
      line-height: 2.16rem;
      color: white;
    }

    &:last-child h3 {
      margin-bottom: .5rem;
    }
  }

  &__noise {
    width: 10.8rem;

    & div:has(span) {
      position: absolute;
      z-index: 1000;
      top: 0;
      left: 0;
      justify-content: space-between;
      width: 100%;
      height: 100%;
    }

    & span {
      display: inline-block;
      width: .8rem;
      height: 100%;
      background-color: #171722;
    }

    & .noUi-handle-lower {
      display: none;
    }

    &.noUi-horizontal {
      height: 2.6rem !important;
    }

    & .noUi-connects {
      border-radius: 0;
      background-color: green;
    }

    & .noUi-connect {
      background-color: yellow;
    }

    & .noUi-handle-upper {
      top: 3rem;
      width: 3.2rem !important;
      border-radius: 0;
      background-image: url(../img/polygon.png);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      cursor: pointer;
    }
  }
}

.check {
  align-items: flex-start;
  justify-content: space-between;

  &__name {
    min-width: 10.4rem;
    font-size: 1.6rem;
    font-weight: 600;
    line-height: 2.16rem;
    color: white;
  }

  &__results {
    flex-direction: column;

    row-gap: 3rem;

    & > div:first-child {
      display: flex;
      align-items: center;

      column-gap: 3rem;
    }
  }

  &__list {
    flex-wrap: wrap;
    max-width: 105rem;

    row-gap: .5rem;
    column-gap: 1rem;

    & li {
      align-items: center;
      padding: .8rem;
      border-radius: 2rem;
      font-size: 1.2rem;
      font-weight: 400;
      line-height: 1.62rem;
      color: white;
      background-color: rgba(41, 112, 25, 1);

      column-gap: .3rem;

      & button {
        width: 1.4rem;
        height: 1.4rem;
        border-radius: 100%;

        & svg {
          width: 100%;
          height: 100%;

          fill: #58aa46;
        }
      }
    }
  }
}

.sort {
  display: none;
  min-width: 22.7rem;
  padding: 2rem;
  border-radius: 1rem;
  opacity: 0;
  background-color: rgba(33, 33, 41, 1);
  transition: opacity .3s ease-in-out;

  &__title {
    margin-bottom: 1rem;
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 2.43rem;
    color: white;
  }

  &__btn {
    position: relative;
    align-items: center;
    padding-left: 3.4rem;
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 2.16rem;
    color: white;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      width: 2.4rem;
      height: 2.4rem;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }

    &_date::after {
      background-image: url(../img/Calendar.png);
    }

    &_priceDown::after,
    &_priceUp::after {
      background-image: url(../img/Arrow.png);
    }

    &_priceDown::after {
      transform: rotate(180deg);
    }

    &_thcDown::after {
      background-image: url(../img/thcDown.png);
      background-size: contain;
    }

    &_thcUp::after {
      background-image: url(../img/thcUp.png);
      background-size: contain;
    }

    &_cbdDown::after {
      background-image: url(../img/cbrDown.png);
      background-size: contain;
    }

    &_cbdUp::after {
      background-image: url(../img/cbrUp.png);
      background-size: contain;
    }
  }

  &__list {
    flex-direction: column;

    row-gap: 1rem;
  }
}

.class {
  position: relative;
  flex-direction: column;
  max-width: 24.3rem;
  padding: 1.5rem 2.2rem 1.5rem 1.7rem;
  border: .1rem solid transparent;
  border-radius: 1rem;
  background: linear-gradient(rgba(53, 53, 66, 1), rgba(53, 53, 66, 1)) padding-box,
  linear-gradient(rgba(132, 213, 44, .36), rgba(23, 23, 34, 0)) border-box;
  transition: min-height .3s ease-in-out;

  row-gap: 1.5rem;

  &::after {
    content: "";
    position: absolute;
    width: calc(100% - .1rem);
    height: calc(100% - .1rem);
    border-radius: 1rem;
    background-image: url(../img/class_back.png);
    background-repeat: no-repeat;
    background-position: left top;
    background-size: cover;

    inset: 0;
  }

  &__content {
    position: relative;
    z-index: 1000;
    display: none;
    flex-direction: column;
    margin-top: 1rem;

    row-gap: .5rem;
  }

  &__title {
    position: relative;
    z-index: 1000;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    font-size: 1.8rem;
    font-weight: 600;
    line-height: 2.43rem;
    color: white;
  }

  &__arrow {
    width: 1.3rem;
    height: .7rem;
    transition: rotate .3s ease-in-out;

    rotate: 180deg;
    fill: #959595;
  }

  &__itemInput {
    align-items: center;
    width: max-content;
    padding-left: 2rem;
    cursor: pointer;

    column-gap: .5rem;
  }

  &__checkClass {
    position: absolute;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
  }

  &__checkDescr {
    font-size: 1.6rem;
    font-weight: 600;
    line-height: 2.352rem;
    color: #959595;
    transition: color .3s ease-in-out;
  }

  &__checkVis {
    position: absolute;
    left: 0;
    align-items: center;
    justify-content: center;
    width: 1.5rem;
    height: 1.5rem;
    border: 1px solid #959595;
    border-radius: .2rem;
    background-color: transparent;
    transition: border-color .3s ease-in-out;

    &::after {
      content: "";
      position: absolute;
      width: .9rem;
      height: .7rem;
      opacity: 0;
      background-image: url(../img/check.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      transition: opacity .3s ease-in-out;
    }
  }
}

.class__checkClass:checked + .class__checkVis::after {
  opacity: 1;
}

.class__checkClass:checked + .class__checkVis {
  border: 1px solid #ffffff;
}

.class__checkClass:checked + .class__checkVis + .class__checkDescr {
  color: rgba(255, 255, 255, 1);
}

//
.none {
  display: none;
}

.visible {
  display: block;
}

.visibleFlex {
  display: flex;
}

.arrowTrans {
  rotate: 0deg;
}

input[type=text]::-ms-clear {
  display: none;
  width: 0;
  height: 0;
}

input[type=text]::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

