  background-color: rgb(23, 23, 34);
}

section {
  max-width: 1920px;
  margin: 0 auto;
}

main {
  flex-grow: 1;
}

footer {
  flex-shrink: 0;
}

a {
  text-decoration: none;
  cursor: pointer;
}

ul {
  margin: 0;
  padding: 0;
