.check {
  align-items: flex-start;
  justify-content: space-between;
}
.check__name {
  min-width: 10.4rem;
  font-size: 1.6rem;
  font-weight: 600;
  line-height: 2.16rem;
  color: white;
}
.check__results {
  flex-direction: column;
  row-gap: 3rem;
}
.check__results > div:first-child {
  display: flex;
  align-items: center;
  column-gap: 3rem;
}
.check__list {
  flex-wrap: wrap;
  max-width: 105rem;
  row-gap: 0.5rem;
  column-gap: 1rem;
}
.check__list li {
  align-items: center;
  padding: 0.8rem;
  border-radius: 2rem;
  font-size: 1.2rem;
  font-weight: 400;
  line-height: 1.62rem;
  color: white;
  background-color: rgb(41, 112, 25);
  column-gap: 0.3rem;
}
.check__list li button {
  width: 1.4rem;
  height: 1.4rem;
  border-radius: 100%;
}
.check__list li button svg {
  width: 100%;
  height: 100%;
  fill: #58aa46;
}

.sort {
  display: none;
  min-width: 22.7rem;
  padding: 2rem;
  border-radius: 1rem;
  opacity: 0;
  background-color: rgb(33, 33, 41);
  transition: opacity 0.3s ease-in-out;
}
.sort__title {
  margin-bottom: 1rem;
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 2.43rem;
  color: white;
}
.sort__btn {
  position: relative;
  align-items: center;
  padding-left: 3.4rem;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.16rem;
  color: white;
}
.sort__btn::after {
  content: "";
  position: absolute;
  left: 0;
  width: 2.4rem;
  height: 2.4rem;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.sort__btn_date::after {
  background-image: url(../img/Calendar.png);
}
.sort__btn_priceDown::after, .sort__btn_priceUp::after {
  background-image: url(../img/Arrow.png);
}
.sort__btn_priceDown::after {
  transform: rotate(180deg);
}
.sort__btn_thcDown::after {
  background-image: url(../img/thcDown.png);
  background-size: contain;
}
.sort__btn_thcUp::after {
  background-image: url(../img/thcUp.png);
  background-size: contain;
}
.sort__btn_cbdDown::after {
  background-image: url(../img/cbrDown.png);
  background-size: contain;
}
.sort__btn_cbdUp::after {
  background-image: url(../img/cbrUp.png);
  background-size: contain;
}
.sort__list {
  flex-direction: column;
  row-gap: 1rem;
}

.class {
  position: relative;
  flex-direction: column;
  max-width: 24.3rem;
  padding: 1.5rem 2.2rem 1.5rem 1.7rem;
  border: 0.1rem solid transparent;
  border-radius: 1rem;
  background: linear-gradient(rgb(53, 53, 66), rgb(53, 53, 66)) padding-box, linear-gradient(rgba(132, 213, 44, 0.36), rgba(23, 23, 34, 0)) border-box;
  transition: min-height 0.3s ease-in-out;
  row-gap: 1.5rem;
}
.class::after {
  content: "";
  position: absolute;
  width: calc(100% - 0.1rem);
  height: calc(100% - 0.1rem);
  border-radius: 1rem;
  background-image: url(../img/class_back.png);
  background-repeat: no-repeat;
  background-position: left top;
  background-size: cover;
  inset: 0;
}
.class__content {
  position: relative;
  z-index: 1000;
  display: none;
  flex-direction: column;
  margin-top: 1rem;
  row-gap: 0.5rem;
}
.class__title {
  position: relative;
  z-index: 1000;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  font-size: 1.8rem;
  font-weight: 600;
  line-height: 2.43rem;
  color: white;
}
.class__arrow {
  width: 1.3rem;
  height: 0.7rem;
  transition: rotate 0.3s ease-in-out;
  rotate: 180deg;
  fill: #959595;
}
.class__itemInput {
  align-items: center;
  width: max-content;
  padding-left: 2rem;
  cursor: pointer;
  column-gap: 0.5rem;
}
.class__checkClass {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}
.class__checkDescr {
  font-size: 1.6rem;
  font-weight: 600;
  line-height: 2.352rem;
  color: #959595;
  transition: color 0.3s ease-in-out;
}
.class__checkVis {
  position: absolute;
  left: 0;
  align-items: center;
  justify-content: center;
  width: 1.5rem;
  height: 1.5rem;
  border: 1px solid #959595;
  border-radius: 0.2rem;
  background-color: transparent;
  transition: border-color 0.3s ease-in-out;
}
.class__checkVis::after {
  content: "";
  position: absolute;
  width: 0.9rem;
  height: 0.7rem;
  opacity: 0;
  background-image: url(../img/check.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: opacity 0.3s ease-in-out;
}

.class__checkClass:checked + .class__checkVis::after {
  opacity: 1;
}

.class__checkClass:checked + .class__checkVis {
  border: 1px solid #ffffff;
}

.class__checkClass:checked + .class__checkVis + .class__checkDescr {
  color: rgb(255, 255, 255);
}

.none {
  display: none;
}

.visible {
  display: block;
}

.visibleFlex {
  display: flex;
}

.arrowTrans {
  rotate: 0deg;
}

input[type=text]::-ms-clear {
  display: none;
  width: 0;
  height: 0;
}

input[type=text]::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}